@import '../../../index.scss';

$headerHeight: 22px;

.addtask-conatainer {
  position: relative;
  .addtask-create-icon {
    position: absolute;
    width: 12px;
    height: 22px;
    line-height: 22px;
    font-weight: bold;
    color: #fff;
    left: 15px;
    font-size: 12px;
  }
}

.addtask-content {
  width: 100%;
  height: 22px;
  border-radius: 20px;
  color: #ffffff;
  background: $PrimaryBgLinear;
  height: $headerHeight;
  font-size: 12px;
  font-family: 'Roboto';
  font-stretch: normal;
  font-style: normal;
  line-height: #{$headerHeight}-3;
  letter-spacing: normal;
  text-align: left;
  padding-left: 20px;
  border: transparent;
}
