  font-size: 12px;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  text-align: left;
  color: #999999;
  padding-left: 28px;
}

.font-weight-bold {
  font-weight: bold;
}

.mr-b-2 {
  margin-bottom: 2px;
}

.flex-2 {
  flex: 2;
}

.flex-1 {
