  }

  .flight-management-text {
    color: #000000;
    opacity: 0.5;
    font-family: Roboto;
    font-size: 14px;
    flex: 1;
    height: $headerHeight;
    line-height: $headerHeight;
  }

  .flight-management-header-right-content {
    flex: 3;
    display: flex;
    justify-content: flex-end;
  }

  .file-upload-search-bar {
    width: 171px;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    height: $headerHeight;
    font-family: Roboto;
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
