  }



  .flight-management-search-bar {
    width: 171px;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    height: $headerHeight;
    font-family: Roboto;
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: left;
    color: #999999;
    padding-left: 28px;
  }

  .ScheduleHeading {
    opacity: 0.5;
    font-family: Roboto;
    font-size: 12px;
    font-weight: 500;
    text-align: left;
    color: #000000;
    margin-bottom: 10px;
    margin-top: 10px;
  }

  //flight list
  .flight-management-list {
    width: $flightListWidth;

    .slider {
      margin-bottom: 5px;

      &.active-slide {
        box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.16);
        border-radius: 10px;
        overflow: hidden;

        .flight-info {
          border-bottom-left-radius: 0;
          border-bottom-right-radius: 0;

          .flgt-mngt-update-button {
            display: block !important;
          }
        }
      }
    }

    .flight-list {
      flex: 1;

      .flight-info {
        display: flex;
        height: 85px;
        padding: 17px;
        background: #fff;
        border-radius: 10px;

        .updatetime-heading {
          margin-top: -4px;
        }

        .updatetime-value {
          padding-top: 15px;
        }

        .updatetimebox-value {
          margin-top: 15px;
          margin-left: 20px;
          margin-right: 5px;
        }

        .updatetime-editheading {
          margin-top: -4px;
          margin-left: 30px;
        }

        .updatetime-value1 {
          margin-top: -7px;
        }

        .update-info {
          display: flex;
          height: 65px;
          padding: 15px;
          color: #999999;
          border-radius: 10px;
        }

        .journey-details {
          font-size: 14px;
          line-height: 16px;

          span {
            color: #000;
            padding-right: 20%;
            font-weight: 600;
          }

          .date {
            color: #f3f4fb;
          }
        }

        .actual-time {
          display: flex;
          font-size: 12px;
          text-transform: uppercase;
          color: $SecondaryTextColor;
          margin-top: 5px;
        }

        .flight-col-list-1 {
          flex: 1;

          .flight-id {
            color: $PrimaryTextColor;
            font-size: 14px;
          }

          .flight-name {
            color: $SecondaryTextColor;
            margin-top: 5px;
            font-size: 12px;
            line-height: 14px;
          }
        }

        .flight-col-list-2 {
          flex: 2;
        }

        .flight-col-list-3 {
          flex: 2;
        }

        .flight-col-list-4 {
          flex: 0.5;
          text-align: center;
        }

        .flight-col-list-5 {
          flex: 1;

          color: $SecondaryTextColor;

          .flgt-mngt-shedule-button {
            margin-bottom: 10px;
            color: $SecondaryTextColor;
            border: 1px solid $SecondaryTextColor;
            background: #fff;
          }

          .flgt-mngt-update-button {
            color: #fff;
            border: none;
            background: $PrimaryBgColor;
            display: none;
          }
        }
      }
    }
  }

  //crew list
  .crew-list {
    flex: 1 1;

    .crew-info {
      display: flex;
      // flex:1;
      height: 85px;
      padding: 17px;
      background: #fff;
      border-radius: 10px;
      border-bottom: inset;
      align-items: center;

      .update-info {
