        font-size: 12px;
        text-transform: uppercase;
        color: $SecondaryTextColor;
        margin-top: 5px;
      }

      .flight-col-list-1 {
        flex: 1;

        .flight-id {
          color: $PrimaryTextColor;
          font-size: 14px;
        }

        .flight-name {
          color: $SecondaryTextColor;
          margin-top: 5px;
          font-size: 12px;
          line-height: 14px;
        }
      }

      .flight-col-list-2 {
        flex: 2;
      }

      .flight-col-list-3 {
        flex: 2;
      }

      .flight-col-list-4 {
        flex: 0.5;
        text-align: center;
      }

      .flight-col-list-5 {
        flex: 1;

        color: $SecondaryTextColor;

        .flgt-mngt-shedule-button {
          margin-bottom: 10px;
          color: $SecondaryTextColor;
          border: 1px solid $SecondaryTextColor;
          background: #fff;
        }

        .flgt-mngt-update-button {
          color: #fff;
          border: none;
          background: $PrimaryBgColor;
          display: none;
        }
      }

      .CrewDetails1 {
        flex: 1;
      }

      .CrewDetails2 {
        flex: 2 1;
      }

      .CrewDetails3 {
        flex: 1;
      }

      .CrewDetails4 {
        flex: 1;
      }

      .CrewDetails5 {
        flex: 2 1;
      }

      .CrewDetails6 {
        flex: 1;
      }

      .CrewDetails7 {
        flex: 1;
      }
    }

    // .CrewOnFlight{
    .CrewDetailsHeader {
      font-family: Roboto;
      font-size: 12px;
      text-align: left;
      color: #666666;
      padding-right: 20px;
    }

    .Rectangle {
      width: 28px;
      height: 16px;
      border-radius: 3px;
      border: solid 1px #f0f0f0;
      background-color: #f0f0f0;
      display: inline-flex;
      margin-right: 5px;
      margin-top: 5px;
    }

    .RectangleFonts {
      width: 20px;
      height: 14px;
      font-family: Roboto;
      font-size: 10px;
      font-weight: 500;
      line-height: 1.67;
      justify-content: center;
      margin-left: 2px;
      margin-right: 2px;
      color: #666666;
    }

    .CrewDetailsName {
      font-family: Roboto;
      font-size: 12px;
      text-align: left;
      color: #666666;
      padding-right: 20px;
      font-weight: 500;
    }
  }

  //crew list
  // }

  .accordian-area {
    width: $AccordianAreaWidth;
    margin-left: 23px;
    margin-right: 13px;

    .file-upload {
      margin: 11px 18px;

      .file-upload-input {
        height: 51px;
        position: relative;
        background: #fff;
        border-radius: 10px;

        .upload-file-content {
          color: $SecondaryTextColor;
          line-height: 51px;
          text-align: center;
          overflow: hidden;
          white-space: nowrap;
          padding: 0 15px;

          span {
            color: $PrimaryTextColor;
            text-decoration: underline;
          }
        }

        input {
          position: absolute;
          width: 100%;
          top: 0;
          left: 0;
          opacity: 0;
          cursor: pointer;
        }
      }
    }

    // file uploading status

    .uploading-status-container {
      margin-top: 15px;

      .uploading-status-head {
        color: $SecondaryTextColor;
      }

      .uploading-status {
        margin-top: 10px;

        .uploading-file {
          height: 36.5px;
          border: solid 1px #f0f0f0;
          background: #fff;
          padding-left: 11px;
          line-height: 36.5px;
          font-size: 12px;
          display: flex;

          &:hover {
            background: $SecondaryBgColor;
          }

          &:first-child {
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
          }

          &:last-child {
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
          }

          .file-name {
            flex: 2.5;
            white-space: nowrap;
            overflow: hidden;
          }

          .file-status {
            flex: 1;
            text-align: right;
            padding-right: 10px;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            span {
              position: relative;
              height: 14px;
              width: 14px;
              justify-content: flex-end;
              display: flex;
              align-items: center;

              svg {
                position: absolute;
                width: 100%;
                height: 100%;
              }
            }
          }
        }
      }
    }

    // uploaded Status

    .uploaded-status-container {
      margin-top: 15px;

      .uploaded-status-head {
        color: $SecondaryTextColor;
        display: flex;
        align-items: center;

        div {
          flex: 1;
        }
      }

      .uploaded-status {
        margin-top: 10px;

        .uploaded-file {
          height: 36.5px;
          border: solid 1px #f0f0f0;
          background: #fff;
          padding-left: 11px;
          line-height: 36.5px;
          font-size: 12px;
          display: flex;

          &:first-child {
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
          }

          &:last-child {
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
          }

          &:hover {
            background: $SecondaryBgColor;
          }

          .file-name {
            flex: 2;
          }

          .file-time {
            flex: 1;
            justify-content: flex-end;
            font-size: 12px;
            color: $SecondaryTextColor;
            text-align: right;
            padding-right: 10px;
            white-space: nowrap;
          }
        }
      }
    }
  }
}

